:root {
    --card-height: 300px;
    --card-width: calc(var(--card-height) / 1.5);
  }
  * {
    box-sizing: border-box;
  }
  body {
    width: 100vw;
    height: 100vh;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #191c29;
  }

  .title {
    width: 100%;
    transition: transform 0.5s;
    border-radius: 30px;
  }
  .card:hover .title {
    transform: translate3d(0%, -50px, 100px);
    border-radius: 20px;  
}
  
  .character {
    width: 100%;
    opacity: 0;
    transition: all 0.5s;
    position: absolute;
    z-index: -1;
    border-radius: 30px;
  }
  
  .card:hover .character {
    opacity: 1;
    transform: translate3d(0%, -30%, 100px);
    border-radius: 20px;
  }
  .log h1{
    position: relative;
    background-color: #191c29;
    color: #fff;
    padding:20px;
    margin-left: 100px;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px solid yellow;
    margin-top: -300px; 
    border-radius: 30px;
    width: 150px;
  }
  .main{
    
    background-color: #c9e8ef;
    width: 500px;
    height: 400px;
    
  }
  .main h1{
    margin-left:100px;
    padding:20px;
    border:none;
    border-radius: 20px;
    color:#fff;
    text-align: center;
  }
  .main a{
    float: left;
    margin-left:-30px;
  }
 .contc ul{
   padding-left: 150px;
