.cardPressedPage{
    width: 90%;
}
.cards-container{
  width: 1200px;
  
}
.card {
    width: 50px; /* Adjust dimensions as needed */
    height: 50px;
    padding: 10px;
    margin: 2px;
    color:#fff;
    text-align: center;
    border: 1px solid black; /* Basic border */
    cursor: pointer; /* Show pointer cursor on hover */
    float: left;  
}
  
  .green {
    background-color: rgb(8, 146, 8); /* Initial green color */
  }
  
  .yellow {
    background-color: rgb(132, 10, 10); /* Color when pressed */
  }
  