nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 10%;
    height:200px;
    background-color: rgb(13, 20, 33);
    margin-left:1250px;
  
  }
  nav button{
    position: fixed;
    margin-left:-1250px;
    margin-top:-19px;
    padding:15px;
    background-color: black;
    border-radius: px;
    border: none;
    transition: 1s;
    cursor: pointer;
  }
  .btc{
    position:fixed;
    height: 150px;
  }
  .btc div{
   
    padding:2px;
    width:30px;
    height:5px;
    background-color:#fff;
    margin:5px;
    cursor: pointer;
  }
  .btc:hover{
    background-color: gold; 
    transition: 1s;
  }
  .btc:hover div{
    background-color:black
  }
  .btc2{
    position: absolute;
    color:white;
    padding:15px;
    margin-left:70px;
    margin-top:-600px;
  }
  nav .navbar {
    position: fixed;
    height: 100%;
    max-width: 1200px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;
  }
  .logoContainer {
    list-style: none;
    position: relative;
    margin-top:-200px;
    padding:5px;
    height:50%;
    border-right:1px solid yellow ;
  }
  .logoContainer h1{
    margin-left:-20px;
  }
  .logoContainer li {
    display: inline-block;
    position: inherit;
    line-height: 70px;
    
  }
  .logoContainer li .logoImg {
    display: inline-block;
    vertical-align: middle;
    line-height: 70px;
    transform: translateY(-10%);
  }
  .logoContainer li{
    float:left;
    margin:3px;
    padding:5px;
  }
  .logoContainer li button{
    background-color: #222933;
    padding:10px;
    border: none;
    border-radius: 5px;
    font-size: 9px;
    margin-left:-5px;
    color:#fff;
    cursor: pointer;
  }
  .logoContainer li button:hover{
    background-color: black;
    transition: 1s;

  }
  nav .navbar .logo {
  
    color: #fff;
    font-size: 30px;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.4s ease;
  }
  nav .navbar .logo:hover {
    color: #04ed93;
  }

 
  nav .navbar .nav-links .links li {
    position: relative;
  
    align-items: center;
    list-style: none;
    padding: 0 14px;
  }
  nav .navbar .nav-links .links li a {
    height: 100%;
    color: #a19797;
    text-decoration: none;
    white-space: nowrap;
    font-size: 22px;
    font-weight: 500;
    font-family: Arial, Helvetica, sans-serif;
    border-bottom: 0px solid transparent;
    transition: all 0.4s ease;
    float: top;
  }
  nav .navbar .nav-links .links li:hover a {
    color: #04ed93;
  }
  nav .navbar .nav-links .links li a hr {
    width: 0px;
    display: hidden;
    border: none;
    border-radius: 70px;
    height: 1.8px;
    background: #04ed93;
    transform: translateY(-1300%);
    transition: all 0.4s ease;
  }
  nav .navbar .nav-links .links li:hover a hr {
    display: block;
    width: 100%;
    transition: all 0.4s ease;
  }
  nav .navbar .bx-menu,
  nav .navbar .nav-links .sidebar-logo .logo_name,
  nav .navbar .sidebar-logo img,
  nav .navbar .nav-links .sidebar-logo .bx-x {
    display: none;
  }
  .logoImg {
    width: 50px;
    height: 50px;
    border-radius: 50px;
  }
  .button-login {
    background: #0d6efd;
    border: 1px solid #0d6efd;
    color: #fff;
    width: 80px;
    padding: 6px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: all 0.2s ease;
  }
  .button-login:hover {
    border: 1px solid #0b5ed7;
    background: #0b5ed7;
  }
  .sidebar-button-login {
    display: none;
  }


 
  
  @media (max-width: 954px) {
    nav .navbar {
      max-width: 100%;
      padding: 0 25px;
    }
    nav .navbar .nav-links .links li {
      padding: 0 10px;
    }
    nav .navbar .nav-links .links li a {
      font-size: 15px;
    }
  }
  @media (max-width: 800px) {
    nav .navbar .nav-links {
      display: block;
      position: fixed;
      background: #242524;
      top: 0;
      left: -300px;
      max-width: 270px;
      width: 100%;
      padding: 20px;
      line-height: 40px;
      transition: all 0.4s ease;
    }
    .logoImg {
      display: none;
    }
    nav .navbar .nav-links.opened {
      left: 0;
    }
    nav .navbar .nav-links .sidebar-logo {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    nav .navbar .bx-menu,
    nav .navbar .nav-links .sidebar-logo .logo_name,
    nav .navbar .nav-links .sidebar-logo .bx-x {
      color: #04ed93;
      font-size: 25px;
      font-weight: 500;
      display: block;
    }
    nav .navbar .sidebar-logo img {
      width: 50px;
      height: 50px;
      display: block;
    }
    nav .navbar .nav-links .links {
      display: block;
      margin-top: 20px;
    }
    nav .navbar .nav-links li a hr,
    nav .navbar .nav-links li:hover a hr {
      display: none;
      width: 0px;
      background: transparent;
    }
    nav .navbar .nav-links .links li {
      display: block;
      border-bottom: none;
    }
    nav .navbar .bx-menu {
      cursor: pointer;
    }
    .button-login {
      display: none;
    }
    .sidebar-button-login {
      display: inline-block;
      background: #0d6efd;
      border: 1px solid #0d6efd;
      color: #fff;
      width: 80px;
      padding: 6px;
      border-radius: 5px;
      cursor: pointer;
      font-size: 16px;
      margin-top: 15px;
    }
  }
