
main {
    background: #bbd4da;
    width: 100%;
    height: 100vh;
  }
  
  
  .header {
    background: rgba(33, 46, 65, 0.8);
    width: 220px;
    height: 650px;
    position: absolute;
    top: 55%;
    left: 12%;
    transform: translate(-50%, -50%);
    border-radius: 7px;
    overflow: hidden;
    z-index: 1;
  }
  
  .smooth li{
    display: block;
    text-decoration: none;
    color: #ffffff;
    width: 100%;
    font-size: 13px;
    position: relative;
    padding: 25px 26px 26px 26px;
    border-bottom: 1px solid rgba(33, 46, 65, 0.8);
    transition: 0.2s linear;
    cursor: pointer;
  }
  
  .smooth li img{
    width: 18px;
    position: inherit;
    vertical-align: -5px;
    margin-right: 5px;
  }
  
  .bottom-line {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: #2980b9;
    transform: scaleX(0);
    transition: 0.2s linear;
    transform-origin: left;
  }
  
  .icon {
    position: absolute;
    display: inline-block;
    left: 70%;
  }
  
  .sectionn {
    color:rgb(161, 51, 51);
    width: 100%;
    max-width: 1000px;
    position: absolute;
    top: 10%;
    height: 60px;
    left: 62%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    color: #ecf0f1;
    z-index: 0;
    font-family: 'Roboto Condensed', sans-serif;
  }

  .sectionn p{
    color:#101010;
    padding:20px;
    margin-left:-10px;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  
  .smooth li:hover {
    background: rgba(22, 35, 54, 0.8);
    padding: 25px 26px 26px 30px;
  }
  
  .smooth li:hover .bottom-line {
    transform: scaleX(1);
  }

  .fixsection{
    width: fit-content;
  }
 
  .fixsection ul li{
    background-color: #101010;
    color:#ffffff;
    float: left;
    list-style: none;
    margin: 10px;
    font-size: 12px;
    text-align: center;
    padding: 10px;
    font-family: Arial, Helvetica, sans-serif;
  }

  .gamegroundd{
    width:90%;
  }
